<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Blog
      </div>
      <v-spacer></v-spacer>
      <v-btn color="primary" @click="create()" depressed elevation="2">
        <v-icon>mdi-plus</v-icon>
        Tambah</v-btn>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table class="elevation-1" :headers="headers" :items="data.data" loading-text="Loading... Please wait" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.Createdate`]="{ item }">
            {{ toDateTime(item.Createdate) }}
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn icon color="success" @click="detail(item)">
            <v-icon small>
              mdi-eye
            </v-icon>
          </v-btn>
          <v-btn icon color="warning" @click="edit(item)">
            <v-icon small>
              mdi-lead-pencil
            </v-icon>
          </v-btn>
          <v-btn icon color="red" @click="hapus(item.id)">
            <v-icon small>
              mdi-delete
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <detailBlog/>
    <editBlog/>
    <createBlog/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import detailBlog from '@/components/Blog/Detail.vue'
import editBlog from '@/components/Blog/Update.vue'
import createBlog from '@/components/Blog/Create.vue'
import Utils from '@/utils/util'
import store from '@/store'
export default {
  name: 'PromotionPage',
  data: () => ({
    search: '',
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        value: 'BlogID'
      },
      { text: 'Judul', sortable: false, value: 'TitleName' },
      { text: 'Kategori', sortable: false, value: 'Category' },
      { text: 'Tags', sortable: false, value: 'Tags' },
      { text: 'Tgl Upload', sortable: false, value: 'Createdate' },
      { text: 'Author', sortable: false, value: 'WritenBy' },
      { text: 'Action', sortable: false, value: 'action' }
    ]
  }),
  components: {
    detailBlog, editBlog, createBlog
  },
  mounted: function () {
    store.dispatch('blog/reqData')
  },
  computed: {
    ...mapState('blog', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('blog', [
      'edit', 'detail', 'hapus', 'create'
    ]),
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    }
  }
}
</script>
